<template>
  <div class="content drawManage">
    <div class="operation">
    <el-form :inline="true" class="mgt20" label-width="100px">
      <el-form-item>
        <el-input placeholder="请输入商户真实姓名" v-model="tempData.realname"><template slot="prepend">商户姓名</template>   </el-input>
      </el-form-item>
      <el-form-item label="提现状态">
          <el-select v-model="tempData.status">
              <el-option v-for="item in statusList" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchList" icon="el-icon-search">查询</el-button>
      </el-form-item>
    </el-form>
    </div>
    <div class="table">
    <el-table v-loading="loading" border :data="tableData">
      <el-table-column label="真实姓名" prop="realname" align="center"></el-table-column>
      <el-table-column label="商家ID" prop="busShopId" align="center"></el-table-column>
      <el-table-column label="申请提现状态" prop="status" align="center">
          <template slot-scope="user">{{statusList[user.row.status].label}}</template>
      </el-table-column>
      <el-table-column label="申请时间" prop="createTime" align="center"></el-table-column>
      <el-table-column prop="cardNumber" label="银行卡号" align="center"></el-table-column>
      <el-table-column prop="balance" label="提现金额" align="center"></el-table-column>
    </el-table>
    </div>
    <pagination
      :current-page="page.pageCurrent"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    ></pagination>
  </div>
</template>

<script>
 import pagination from "@/components/pagination"
 import {withDraw} from "@/api/index.js"
export default {
     components: {
        pagination
     },
     data(){
         return{
           statusList:[
             {label:'待处理',value:1},{label:'提现成功',value:0},{label:'提现失败',value:2}
           ],
       //加载状态
      loading: false,
      //表格数据
      tableData: [],
      //搜索时临时数据
      tempData: {
      },
      //搜索时用的数据
      searchData: {},
      //分页数据
      page: {
        pageNum: null,
        pageCurrent: 1,
        pageSize: 5
      },
         }
     },
     methods:{
    initData: function() {
      this.loading = true;
      this.searchData=this.$common.deepCopy(this.tempData)
      this.searchData.page=this.page.pageCurrent
      this.searchData.size=this.page.pageSize
      withDraw.merchant(this.searchData
      ).then(res =>{
        this.tableData = res.rows;
        this.page.pageNum = res.total_record;
      });
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
         //条件查询
   searchList: function () {
        this.searchData=this.$common.deepCopy(this.tempData)
        this.page.pageCurrent = 1;
        this.initData(1)
   },
    //页数改变时的回调
    currentChange(val) {
      this.page.pageCurrent = val;
      this.initData();
    },
    //每页总数改变时的回调
    sizeChange(val) {
      this.page.pageSize=val
      this.page.pageCurrent = 1;
      this.initData(1);
    },
     },
     beforeMount() {
    this.initData();
  }

}
</script>

<style>

</style>



